@import "_global";

.project-card{
    border-radius: 10px !important;
    background-color: transparent !important;
}

.project-card-content{
    height: 110px;
    background-color: var(--monokai-semi-black);
}

.project-card-footer{
    background-color: var(--monokai-semi-black);
    flex-wrap: wrap
}

.project-card-title{
    font-family: Kano !important;
    font-weight: normal;
    color: var(--monokai-white);
}

.project-card-action-area{
    position: relative;
}

.project-card-desc{
    font-family: FiraSansLight !important;
    color: var(--monokai-semi-white);
    letter-spacing: 0.5px !important;
}

@keyframes blur {
    0% { filter: blur(0px);}
    100% { filter: blur(4px);}
}

.project-card-thumbnail{
    height: 200px;
    transition: filter 1s;

    &.enable-hover
    {
        :hover > &{
            animation: blur 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
            animation-fill-mode: forwards;
        }
    }
}

.wipText{
    font-size: 14px;
    color: var(--monokai-green);
}

.project-card-chip{
    font-family: "FiraSans" !important;
    border-radius: 5px !important;
    margin: 5px 0px;
}

.red{
    background-color: var(--monokai-red) !important;
    color: var(--monokai-black) !important;

    &.lite{
        background-color: initial !important;
    }
}

